import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '../views/Layout.vue'
import Js from '../views/Js.vue'
import Vuee from '../views/Vuee.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Layout',
    component: Layout
  },
  {
    path: '/jS',
    name: 'JS',
    component: Js
  },
  {
    path: '/vue',
    name: 'Vue',
    component: Vuee
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
